// TechBubble Component

import React, { lazy, Suspense} from 'react'
import './tech-bubble.scss'
import {Link} from 'react-router-dom'

const Img = lazy(() => import('../Img'))

const TechBubble = ({ img, name, url, }) => {
  
  return (
    <figure className="techBubble">
      <Link to={`/technologies/${url}`} aria-label={name}>
        <Suspense fallback={null}>
          <Img path={`techs/${img}`} alt={name} />
        </Suspense>
      </Link>
      <figcaption>
        {name}
      </figcaption>
    </figure>
  )
}

export default TechBubble
