import React, { lazy, Suspense} from 'react'
import './cert-bubble.scss'
import {Link} from 'react-router-dom'

const Img = lazy(() => import('../Img'))

const CertBubble = ({img, name, uri}) => {
  
  return (
      <figure className="certBubble">
      <Link to={`/training/${uri}`} aria-label={name}>
        <Suspense >
          <Img path={`techs/${img}`} alt={name} />
        </Suspense>
      </Link>
      <figcaption>
        {name}
      </figcaption>
    </figure>
  )
}

export default CertBubble
